import {
	Color,
	HemisphereLight,
	EquirectangularReflectionMapping,
	MathUtils,
	Vector3,
} from 'three';

import { GLTFLoader } from 'three/examples/jsm/loaders/GLTFLoader.js';
import { RGBELoader } from 'three/examples/jsm/loaders/RGBELoader.js';

const { min, tan } = Math;
const { degToRad } = MathUtils;

const gltfLoader = new GLTFLoader();
const rgbeLoader = new RGBELoader();

function calcCameraDistance(vFov) {
	function distance(modelBoundingRadius, fov) {
		return modelBoundingRadius / tan(0.5 * degToRad(fov));
	}
	const hFov = window.innerWidth * vFov / window.innerHeight;
	return window.innerWidth > window.innerHeight ? distance(4, vFov) : distance(3, hFov);
}

export default function setup(scene, camera, { pmremGenerator }) {

	rgbeLoader
		.load('textures/equirectangular/lilienstein_1k.hdr', function (texture) {
			texture.mapping = EquirectangularReflectionMapping;
			scene.environment = texture;
		} );

	const cameraDistance = calcCameraDistance(camera.fov);
	camera.position.set(-2, 1, 3);
	camera.position.setLength(cameraDistance);

	scene.background = new Color(0x9bc4e0);

	const hemiLight = new HemisphereLight(0xffffff, 0x444444, 0.5);
	hemiLight.position.set(0, 10, 0);

	scene.add(hemiLight);

	gltfLoader.load('models/pumphouse.glb', function (gltf) {
		const model = gltf.scene;
		scene.add(model);
	});
}
