import {
  ACESFilmicToneMapping,
  PerspectiveCamera,
  PMREMGenerator,
  Scene,
  WebGLRenderer,
  sRGBEncoding,
} from 'three';

import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls.js';

import setup from './setup.js';

function setFromConfig(obj, config) {
  Object.entries(config).forEach(function ([key, value]) {
    if (Array.isArray(value)) {
      obj[key].set(...value);
    }
    else {
      obj[key] = value;
    }
  })
}

const config = {
  camera: {
    fov: 45,
    near: 0.5,
    far: 50,
  },
  controls: {
    minDistance: 4,
    maxDistance: 20,
    target: [0, 1.5, 0],
    enablePan: false,
    enableDamping: true,
  },
  renderer: {
    toneMapping: ACESFilmicToneMapping,
    toneMappingExposure: 1,
    outputEncoding: sRGBEncoding,
  },
  frameDelay: 50,
};

const scene = new Scene();
const camera = new PerspectiveCamera(
  config.camera.fov,
  window.innerWidth / window.innerHeight,
  config.camera.near,
  config.camera.far
);

const renderer = new WebGLRenderer();
renderer.setPixelRatio(window.devicePixelRatio);
renderer.setSize(window.innerWidth, window.innerHeight);
Object.assign(renderer, config.renderer);
document.body.appendChild(renderer.domElement);

const controls = new OrbitControls(camera, renderer.domElement);
setFromConfig(controls, config.controls);
controls.update();

const callbacks = setup(scene, camera, {
  get pmremGenerator() {
    const value = new PMREMGenerator(renderer);
    Object.defineProperty(this, 'pmremGenerator', { value });
    return value;
  },
});

function onWindowResize() {
  camera.aspect = window.innerWidth / window.innerHeight;
  camera.updateProjectionMatrix();
  renderer.setSize( window.innerWidth, window.innerHeight );
}

function animate() {
  if (config.frameDelay){
    window.setTimeout(() => requestAnimationFrame(animate), config.frameDelay);
  }
  else {
    requestAnimationFrame(animate);
  }
  if (callbacks?.update) {
    const t = performance.now();
    callbacks.update(t);
  }
  controls.update();
  renderer.render(scene, camera);
}

window.addEventListener('resize',onWindowResize);
animate();
